.App{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container{
  width: 11.5cm;
  height: 13cm;
  border-radius: 10px;
  border: .5px solid lightgray;
}
.Glogo-container{
  display: flex;
  justify-content: center;
}
.Glogo{
  width: 2cm;
  /* height: 2cm; */
}
.sign-in{
  padding-top: 10px;
  margin: 0px 0px;
  font-size: 26px;
}
.continue{
  padding-top: 7px;
  font-size: 16px;
}
.signin-container{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input-container{
  padding-left: 1cm;
  padding-right: 1cm;
  display: flex;
  flex-direction: column;
}

.input{
  border: .5px solid lightgray;
  height: 1.3cm;
  font-size: 17px;
  padding-left: .5cm ;
  border-radius: 5px;
}
.forgot{
  font-size: 15px;
  font-weight: 600;
  padding-top: 7px;
  color: #1a73e8;
  cursor: pointer;
}
.guest{
  font-size: 14px;
  /* font-weight: 500; */
}
.learn{
  font-size: 15px;
  font-weight: 600;
  padding-top: 1px;
  color: #1a73e8;
  cursor: pointer;
}

.buttons{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  row-gap: 1cm;
}
.create{
  font-size: 15px;
  font-weight: 600;
  /* padding-top: 1px; */
  color: #1a73e8;
  cursor: pointer;
}
.next{
  width: 2cm;
  height: 1cm;
  border: none;
  border-radius: 5px;
  background-color: #1a73e8;
  color: whitesmoke;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
}

@media screen and (max-width:500px){
  .App{
    align-items: flex-start;
  }
  .container{
    border: none;
    width: 100vw;
   
  }
  
}
@media screen and (max-width:300px){
  .buttons{
    flex-direction: column-reverse;
    align-items: center;
  }
}